<template>
	<div style="margin:0 20px;">
		<table></table>
		<h2 style="margin:20px 0;">{{data.title}}</h2>
		<div v-html="unescapeHTML(data.content)"></div>
		<!-- <s:property :value="data.content" escape="false" /> -->
	</div>
</template>
<script>
import { gethelp_detail } from "@/axios/getData";
export default {
	data(){
		return{
			data:{}
		}
	},
	created(){
		this.getdata();
	},
	methods:{
		async getdata(){
			var data = {};
			data.article_id = this.$route.query.article_id
			var res = await gethelp_detail(data)
			this.data = res.data
		},
		unescapeHTML(a){
			a = "" + a;
			var str=a.replace(/&lt;/g, "<").replace(/&gt;/g, ">").replace(/&amp;/g, "&").replace(/&quot;/g, '"').replace(/&apos;/g, "'");
			var re = new RegExp('<[^<>]+>','g');
			var text = str.replace(re ,"");
      return text;
    }	
	}
}
</script>
<style lang="scss" scoped>
	
</style>


